.service-container{
    text-align: center;
}

.service-cards{
    display: flex;
    flex-wrap: wrap;

    justify-content: center;
    align-items: center;
}

.service-card{
    flex-basis: 400px;
    min-height: 300px;

    margin: 10px;

    border-radius: 2px;
    box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.1);

    transform: scale(0.9);
}

.service-card-animation{
    animation: card-animation 5s ease forwards;
}

.svg-style-1{
    width: 200px;
    height: 200px;
}

.svg-style-2{
    width: 270px;
    height: 225px;
}

.svg-style-3{
    width: 230px;
    height: 225px;
}

.svg-style-4{
    width: 230px;
    height: 255px;
}

@keyframes card-animation{
    2%{
        box-shadow: 2px 2px 7px 5px rgba(0, 0, 0, 0.1);
        transform: scale(1);
    }
    90%{
        box-shadow: 2px 2px 7px 5px rgba(0, 0, 0, 0.1);
        transform: scale(1);
    }
    100%{
        box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.1);
        transform: scale(0.9);
    }
}